import { ReactNode } from 'react';
import NextLink from 'next/link';
import Button from "./Button.styles";

const Link = ({
  children,
  href,
  button = false,
}: {
  children: ReactNode;
  href: string;
  button?: boolean;
}) => {
  if (button) {
    return  <Button role={'link'}>
      <NextLink href={href}>
        {/* eslint-disable-next-line jsx-a11y/anchor-is-valid */}
        <a>{children}</a>
      </NextLink>
    </Button>
  }
  return      <NextLink href={href}>
      {/* eslint-disable-next-line jsx-a11y/anchor-is-valid */}
      <a>{children}</a>
    </NextLink>
}


export default Link;
